import React from "react";
import PropTypes from "prop-types";
import { makeStyles } from "@material-ui/core/styles";
import { Link } from "react-router-dom";
import Button from "@material-ui/core/Button";
import styles from "./header.module.css";

const useStyles = makeStyles({
  label: {
    textTransform: "capitalize",
  },
});


export const Header = ({ name, navs }) => {
  const classes = useStyles();

  return (
    <header>
      <div className={styles.wrapper}>
      <Link to={'/blog-gary'}>
        <div>
          <img
            src='data:image/svg+xml;base64,'
            alt='Avatar'
            width='50'
            height='50'
            className={styles.img}
          />
          <h1 className={styles.h1}>{name}</h1>
        </div>
        </Link>
        <div>
          <>
            {navs.map(({ desc, to }) => {
              return (
                <Link to={to}>
                  <Button size="large"
                    classes={{
                      root: classes.contactMe,
                      label: classes.label,
                    }}
                  >
                    <strong>{desc}</strong>
                  </Button>
                </Link>
              );
            })}
            {/* <Button> <strong>Home</strong> </Button>
        <Button> <strong>About Me</strong> </Button>
        <Button> <strong>TBD</strong> </Button> */}
          </>
        </div>
      </div>
    </header>
  );
};

export default Header;

Header.propTypes = {
  name: PropTypes.string,
  navs: PropTypes.shape({}),
};

Header.defaultProps = {
  name: "Your Name",
  navs: [
    { desc: "Home", to: "/blog-gary" },
    { desc: "About", to: "/about" },
    { desc: "Contact", to: "/contact"},
    { desc: "TBD", to: "/404" },
  ],
};
