import React from "react";
import { useHistory } from "react-router-dom";

import styles from "./Card.module.css";

const Card = ({ blog, tagsMeta }) => {
  let history = useHistory();

  const redirect = (id) => {
    history.push(`/post/${id}`);
  };


  const showCardTags = blog['tag'].map((label) => {
      return (<a
        key={tagsMeta[label].id}
        className={`${tagsMeta[label].className} ${styles["my-label"]}`}
      >
        {label}
      </a>)
  })


  return (
    <div class={`ui card ${styles["card-size"]}`} onClick={()=>redirect(blog.id)}>
      <div class='content'>
        <div>
          {showCardTags}
        </div>
        <div className={styles["my-header"]}>
          {blog.title}
        </div>

        <div class={styles["my-desc"]}>
          <p>
            {blog.content}
          </p>
        </div>

        <div class='header'>
          <img
            class='left floated ui image'
            width='50'
            height='50'
            src='data:image/svg+xml;base64,'
            alt='profile_img'
          />
          <span class='header'>
            Linchen
            <div class='meta' style={{ marginTop: "5px" }}>
              <span class='time'>{blog.date}</span>
              <span class='right floated category'>
                <i class='clock outline icon' style={{ marginRight: "5px" }} />
                10 mins
              </span>
            </div>
          </span>
        </div>
      </div>
    </div>
  );
};

export default Card;
